/* App.vue css */

.row {
  margin: 0;
  padding: 0;
}

* {
  font-family: tahoma, "microsoft yahei", "\5FAE\8F6F\96C5\9ED1" !important;
}

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
}

.fwdt {
  padding: 0px;
  margin-top: 60px;
  max-width: 1100px;
  min-width: 900px;
}

.fwdt .row {
  width: 100%;
  overflow: hidden;
  margin-left: 0;
}

.bodybg1 {
  background: url(../images/bodybg.jpg) no-repeat fixed left top;
  background-size: cover;
}

/*nav header*/

.navbar-brand {
  float: left;
  height: 68px;
  padding: 15px 15px;
  font-size: 18px;
  line-height: 37px;
  margin-left: -40px !important;
  margin-top: -7px;
}

.navbar-wrapper {
  background: #2479B6;
  height: 68px;
}

.f-r {
  float: right;
}

.navbar-nav > li > a {
  line-height: 38px;
  text-align: center;
  font-size: 16px;
  font-weight: 400;
  min-width: 86px;
}

.navbar-inverse {
  background: none;
  border: 0;
}

.navbar-inverse .navbar-nav > li > a {
  color: #fff;
}

.navbar-inverse .navbar-nav > .active > a,
.navbar-inverse .navbar-nav > .active > a:hover,
.navbar-inverse .navbar-nav > .active > a:focus {
  background-color: #2491E1
}

/*屏幕尺寸*/

@media ( max-width: 1020px) {
  .container {
    width: 990px;
  }

  .row {
    height: 202px;
    word-break: keep-all;
    /* 不换行 */
    white-space: nowrap;
    /* 不换行 */
    overflow: hidden;
  }

  .fwdt .row .col-md-4 {
    height: 202px;
    width: 40%;
    padding: 0;
  }

  .fwdt .row .col-md-2 {
    height: 200px;
    width: 20%;
    padding: 0;
  }
}

@media ( min-width: 1023px) {
  .container {
    width: 1000px;
  }

  .fwdt .row .col-md-4 {
    height: 202px;
    width: 40%;
    padding: 0;
  }

  .fwdt .row .col-md-2 {
    height: 202px;
    width: 20%;
    padding: 0;
  }
}

@media ( min-width: 1279px) {
  .container {
    width: 1270px
  }
}

@media ( min-width: 1365px) {
  .container {
    width: 1300px
  }
}

.tzgg {
