}

.tzgg div,
.ldgx div,
.sbk div,
.shbz div,
.zcfg div,
.rshx div,
.jy div,
.rsrc div,
.grxx div,
.wdsc div,
.xxzx div,
.fwzy div,
.gd div {
  border: 3px solid #000;
  border-top: 0;
  border-left: 0;
}

.fwdt .col-md-2 img {
  -webkit-transition: -webkit-transform 0.3s;
  /* For Safari 3.1 to 6.0 */
  transition: transform 0.3s;
}

.shbz:hover img,
.rshx:hover img,
.ldgx:hover img,
.fwzy:hover img,
.wdsc:hover img,
.grxx:hover img,
.gd:hover img {
  -webkit-transform: rotate(360deg);
  /* Chrome, Safari, Opera */
  transform: rotate(360deg);
}

