
.slider-wrapper {
    position: relative;
    overflow: hidden;
}
.slider-wrapper,
.slider {
    height: 100%;
    width: 100%;
}
.slider {
    display: none;
}
.slider-box {
    position: absolute;
    top: 0;
    left: 0;
}
.slider.active {
    display: block;
}
.slider-pager-wrapper {
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    top: 50%;
    right: 20px;
    transform: translateY(-50%);
}
.slider-pager {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: #999;
}
.slider-pager.active {
    background: #666;
}
#lb {
    width: 100%;
    position: fixed;
    top: 68px;
    bottom: 0px;
    left: 0px;
}



